import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html',
  styleUrls: ['./location.component.css']
})
export class LocationComponent implements OnInit {

  createLocationForm: FormGroup;
  editLocationForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public locationdata: any;
  public locationEditdata:any
  public currentUser: any;
  public showModal:any;
  public locationData:any;
  public LocationDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 
      this.blockUI.start('Loading...'); // Start blocking

      this.locationdata = {};
    this.locationEditdata={};
    }

  ngOnInit() {
    this.orgGetAllData();

    this.locationGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createLocationForm = this.formBuilder.group({
  organization: ['', Validators.required],
  location:['',Validators.required]
 });

 this.editLocationForm = this.formBuilder.group({
  locationId:[''],
  organization:[''],
  location:['']
 });

  }


  orgGetAllData(){
    this.dataService.getAllOrganization().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.organizationData=data.organization_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Orgnization get error", JSON.stringify(err.message));
			}
		);
  }
  locationFormSubmit() {
		this.submitted = true;
		if (this.createLocationForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let locationFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.locationdata);

		locationFormData.append('user_id', this.currentUser.id);
    locationFormData.append('organization', this.locationdata.organization);
    locationFormData.append('location', this.locationdata.location);

		

		 //console.log(locationFormData);
		this.dataService.createLocation(locationFormData).subscribe(
			data => {
      //  console.log(data);
        this.createLocationForm.reset();
        this.locationGetAllData();
        
			},
			err => {
				console.log("Location Creation error", JSON.stringify(err.message));
			}
		);
  }

  locationGetAllData(){
    this.dataService.getAllLocation().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.locationData=data.location_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();

				}
			},
			err => {
				console.log("Location get error", JSON.stringify(err.message));
			}
		);
  }

  locationEdit(LocationAllData){
    //console.log(LocationAllData.id);
    this.dataService.getLocationDetails(LocationAllData.id).subscribe(
			data => {
      //console.log(data.Location_details);
      this.LocationDetails=data.Location_details;

      this.editLocationForm.patchValue({
        locationId:data.Location_details[0]['id'],
        location:data.Location_details[0]['location'],
        organization:data.Location_details[0]['org_id'],

        


      });


				
			}
		);

  }

  locationFormUpdate() {
		this.submitted = true;
		if (this.editLocationForm.invalid) {
			return;
    }
    
    
    let locationUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
   // console.log(this.locationEditdata);

    locationUpdateFormData.append('user_id', this.currentUser.id);
    locationUpdateFormData.append('location_id', this.locationEditdata.locationId);
    locationUpdateFormData.append('organization', this.locationEditdata.organization);
    locationUpdateFormData.append('location', this.locationEditdata.location);

		
		// console.log(locationUpdateFormData);
		this.dataService.updateLocation(locationUpdateFormData).subscribe(
			data => {
        //console.log(data);

          this.locationGetAllData();

        


			
			},
			err => {
				console.log("Location Creation error", JSON.stringify(err.message));
			}
		);
  }

  locationDelete(LocationAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteLocationDetails(LocationAllData.id).subscribe(
               data => {
             // console.log(data);
              this.locationGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}




