import { NgModule } from '@angular/core';
import { Routes, RouterModule, CanActivate } from '@angular/router';
import { LoginComponent } from './components/login/login.component'; 
import { MainComponent } from './components/main/main.component'; 
import { SurveyComponent } from './components/survey/survey.component'; 
import { DashboardComponent } from './components/dashboard/dashboard.component'; 
import { RegisterComponent } from './components/register/register.component'; 
import { AprojectsComponent } from './components/admin/projects/projects.component'; 
import { AprojectComponent } from './components/admin/project/project.component'; 
import { LSReportComponent } from './components/lsreport/lsreport.component'; 

import { DlregistrationComponent } from './components/dl/dlregistration/dlregistration.component';
import { OrganizationComponent } from './components/admin/organization/organization.component';
import { DlregisterComponent } from './components/dlregister/dlregister.component';
import { LregisterComponent } from './components/lregister/lregister.component';
import { AddprojectComponent } from './components/admin/addproject/addproject.component';
 import { BeatssurveyComponent } from './components/beatssurvey/beatssurvey.component';
import { BeatssurveychatComponent } from './components/beatssurveychat/beatssurveychat.component';
import { BeatsdashboardComponent } from './components/admin/beatsdashboard/beatsdashboard.component';

import { AuthGuard } from './guards/auth.guard'; 

// master admin dashboard
import { AdmindashboardComponent} from './components/masteradmin/admindashboard/admindashboard.component';
import { AdminorganizationComponent} from './components/masteradmin/adminorganization/adminorganization/adminorganization.component';
import {QuestiontypeComponent } from './components/masteradmin/questiontype/questiontype.component';
import {DimensionComponent} from './components/masteradmin/dimension/dimension.component';
import {SubdimensionComponent} from './components/masteradmin/subdimension/subdimension.component';
import {TenureComponent} from './components/masteradmin/tenure/tenure.component';
import {PerformanceComponent } from './components/masteradmin/performance/performance.component';
import { LocationComponent } from './components/masteradmin/location/location.component';
import { LevelsComponent } from './components/masteradmin/levels/levels.component';
import { EmployeeEventComponent } from './components/masteradmin/employee-event/employee-event.component';
import { BusinessUnitComponent} from './components/masteradmin/business-unit/business-unit.component';
import { AgeComponent } from './components/masteradmin/age/age.component';
import { LikertOptionsComponent} from './components/masteradmin/likert-options/likert-options.component';
import { EmployeeComponent} from './components/masteradmin/employee/employee.component';
import { LikertScaleComponent } from './components/masteradmin/likert-scale/likert-scale.component';
import { OpenSpaceComponent } from './components/masteradmin/open-space/open-space.component';
import { MCQComponent } from './components/masteradmin/mcq/mcq.component';

const routes: Routes = [
    { path: '', redirectTo: '/main', pathMatch: 'full' },
    { path: 'login', component: LoginComponent },
    { path: 'main', component: MainComponent, canActivate: [AuthGuard] },
    { path: 'admin/addproject', component: AddprojectComponent, canActivate: [AuthGuard] },
    { path: 'dashboard/:projectId', component: DashboardComponent, canActivate: [AuthGuard] },
   

    { path: 'survey/:surveyCode', component: SurveyComponent },
    { path: 'survey', component: SurveyComponent },
    { path: 'register/:registerCode', component: RegisterComponent },
    { path: 'register', component: RegisterComponent },
    { path: 'admin/projects', component: AprojectsComponent, canActivate: [AuthGuard] },
    { path: 'admin/project/:projectId', component: AprojectComponent, canActivate: [AuthGuard] },
    { path: 'lsreport/:lrId', component: LSReportComponent, canActivate: [AuthGuard] },
    { path: 'dl/dlregistration', component: DlregistrationComponent },
    { path: 'admin/organization', component: OrganizationComponent, canActivate: [AuthGuard] },
    { path: 'beatssurvey/:surveyCode', component: BeatssurveyComponent },
    { path: 'beatssurveychat/:surveyCode', component: BeatssurveychatComponent },
    { path: 'admin/dashboard/:projectId', component: BeatsdashboardComponent, canActivate: [AuthGuard] },



     { path: 'admindashboard', component: AdmindashboardComponent, canActivate: [AuthGuard] },
    { path: 'aorganization', component: AdminorganizationComponent, canActivate: [AuthGuard] },
    { path: 'questionType', component: QuestiontypeComponent, canActivate: [AuthGuard] },
    { path: 'dimension', component: DimensionComponent, canActivate: [AuthGuard] },
    { path: 'subdimension', component: SubdimensionComponent, canActivate: [AuthGuard] },
    { path: 'tenure', component: TenureComponent, canActivate: [AuthGuard] },
    { path: 'performance', component: PerformanceComponent, canActivate: [AuthGuard] },
    { path: 'location', component: LocationComponent, canActivate: [AuthGuard] },
    { path: 'levels', component: LevelsComponent, canActivate: [AuthGuard] },
    { path: 'employeeEvent', component: EmployeeEventComponent, canActivate: [AuthGuard] },
    { path: 'businessUnit', component: BusinessUnitComponent, canActivate: [AuthGuard] },
    { path: 'age', component: AgeComponent, canActivate: [AuthGuard] },
    { path: 'likertOptions', component: LikertOptionsComponent, canActivate: [AuthGuard]},
    { path: 'employee', component: EmployeeComponent, canActivate: [AuthGuard]},
    { path: 'likertScale', component: LikertScaleComponent, canActivate: [AuthGuard]},
    { path: 'openSpace', component: OpenSpaceComponent, canActivate: [AuthGuard]},
    { path: 'mcq', component: MCQComponent, canActivate: [AuthGuard]}

];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
