import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';
@Component({
  selector: 'app-subdimension',
  templateUrl: './subdimension.component.html',
  styleUrls: ['./subdimension.component.css']
})
export class SubdimensionComponent implements OnInit {
  
  createSubdimensionForm: FormGroup;
  editSubdimensionForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public subdimensiondata: any;
  public subdimensionEditdata:any
  public currentUser: any;
  public showModal:any;
  public subdimensionData:any;
  public SubDimensionDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  

  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 

      this.blockUI.start('Loading...'); // Start blocking


      this.subdimensiondata = {};
    this.subdimensionEditdata={};
    }

  ngOnInit() {
    this.dimensionGetAllData();

    this.subdimensionGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createSubdimensionForm = this.formBuilder.group({
  dimension: ['', Validators.required],
  subdimension:['',Validators.required]
 });

 this.editSubdimensionForm = this.formBuilder.group({
  subdimensionId:[''],
  dimension:[''],
  subdimension:['']
 });

  }


  dimensionGetAllData(){
    this.dataService.getAllDimension().subscribe(
			data => {
			
				if(data.message == 'Success') {
        //	this.lastProjId = data.project_id;
        //console.log('Hiiii');
       //console.log(data);
        this.dimensionData=data.dimension_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();

				}
			},
			err => {
				console.log("Dimension get error", JSON.stringify(err.message));
			}
		);
  }

  subdimensionFormSubmit() {
		this.submitted = true;
		if (this.createSubdimensionForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let subdimensionFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.subdimensiondata);

		subdimensionFormData.append('user_id', this.currentUser.id);
    subdimensionFormData.append('dimension', this.subdimensiondata.dimension);
    subdimensionFormData.append('sub_dimension', this.subdimensiondata.subdimension);

		

		// console.log(subdimensionFormData);
		this.dataService.createSubdimension(subdimensionFormData).subscribe(
			data => {
        //console.log(data);
        this.subdimensionGetAllData();
        this.createSubdimensionForm.reset();
        
			},
			err => {
				console.log("Sub Dimension Creation error", JSON.stringify(err.message));
			}
		);
  }

  subdimensionGetAllData(){
    this.dataService.getAllSubdimension().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.subdimensionData=data.subdimension_details;
        //	this.changepanel('queDetails');
        
				}
			},
			err => {
				console.log("Sub Dimension get error", JSON.stringify(err.message));
			}
		);
  }

  subdimensionEdit(SubdimensionAllData){
    //console.log(SubdimensionAllData.id);
    this.dataService.getSubdimensionDetails(SubdimensionAllData.id).subscribe(
			data => {
     // console.log(data.Subdimension_details);
      this.SubDimensionDetails=data.Subdimension_details;

      this.editSubdimensionForm.patchValue({
        subdimensionId:data.Subdimension_details[0]['id'],
        subdimension:data.Subdimension_details[0]['sub_dimension_text'],
        dimension:data.Subdimension_details[0]['dimension_id'],

        


      });


				
			}
		);

  }

  subdimensionFormUpdate() {
		this.submitted = true;
		if (this.editSubdimensionForm.invalid) {
			return;
    }
    
    
    let subdimensionUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.subdimensionEditdata);

    subdimensionUpdateFormData.append('user_id', this.currentUser.id);
    subdimensionUpdateFormData.append('subdimension_id', this.subdimensionEditdata.subdimensionId);
    subdimensionUpdateFormData.append('dimension', this.subdimensionEditdata.dimension);
    subdimensionUpdateFormData.append('sub_dimension', this.subdimensionEditdata.subdimension);

		
		 //console.log(subdimensionUpdateFormData);
		this.dataService.updateSubdimension(subdimensionUpdateFormData).subscribe(
			data => {
     //   console.log(data);

          this.subdimensionGetAllData();

        


			
			},
			err => {
				console.log(" Sub Dimension Creation error", JSON.stringify(err.message));
			}
		);
  }

  subdimensionDelete(SubdimensionAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteSubdimensionDetails(SubdimensionAllData.id).subscribe(
               data => {
            // console.log(data);
              this.subdimensionGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}

