import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';
@Component({
  selector: 'app-dimension',
  templateUrl: './dimension.component.html',
  styleUrls: ['./dimension.component.css']
})
export class DimensionComponent implements OnInit {

 
  createDimensionForm: FormGroup;
  editDimensionForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public dimensiondata: any;
  public dimensionEditdata:any
  public currentUser: any;
  public showModal:any;
  public dimensionData:any;
  public DimensionDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  

  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 

      this.blockUI.start('Loading...'); // Start blocking


      this.dimensiondata = {};
    this.dimensionEditdata={};
    }

  ngOnInit() {
    this.dimensionGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createDimensionForm = this.formBuilder.group({
  dimension: ['', Validators.required]
 });

 this.editDimensionForm = this.formBuilder.group({
  dimensionId:[''],
  dimension:['']
 });

  }

  dimensionFormSubmit() {
		this.submitted = true;
		if (this.createDimensionForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let dimensionFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
   // console.log(this.dimensiondata);

		dimensionFormData.append('user_id', this.currentUser.id);
		dimensionFormData.append('dimension', this.dimensiondata.dimension);
		

		 //console.log(dimensionFormData);
		this.dataService.createDimension(dimensionFormData).subscribe(
			data => {
       // console.log(data);
        this.dimensionGetAllData();

        this.createDimensionForm.reset();
        
			},
			err => {
				console.log("Dimension Creation error", JSON.stringify(err.message));
			}
		);
  }

  dimensionGetAllData(){
    this.dataService.getAllDimension().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.dimensionData=data.dimension_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();

				}
			},
			err => {
				console.log("Dimension get error", JSON.stringify(err.message));
			}
		);
  }

  dimensionEdit(DimensionAllData){
  //  console.log(DimensionAllData.id);
    this.dataService.getDimensionDetails(DimensionAllData.id).subscribe(
			data => {
     // console.log(data.QuestionType_details);
      this.DimensionDetails=data.Dimension_details;

      this.editDimensionForm.patchValue({
        dimensionId:data.Dimension_details[0]['id'],
        dimension:data.Dimension_details[0]['dimension_text'],
        


      });


				
			}
		);

  }

  dimensionFormUpdate() {
		this.submitted = true;
		if (this.editDimensionForm.invalid) {
			return;
    }
    
    
    let dimensionUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
  //  console.log(this.dimensionEditdata);

    dimensionUpdateFormData.append('user_id', this.currentUser.id);
    dimensionUpdateFormData.append('dimension_id', this.dimensionEditdata.dimensionId);
		dimensionUpdateFormData.append('dimension', this.dimensionEditdata.dimension);
		
		// console.log(dimensionUpdateFormData);
		this.dataService.updateDimension(dimensionUpdateFormData).subscribe(
			data => {
        //console.log(data);

          this.dimensionGetAllData();

        


			
			},
			err => {
				console.log("Dimension Creation error", JSON.stringify(err.message));
			}
		);
  }

  dimensionDelete(DimensionAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteDimensionDetails(DimensionAllData.id).subscribe(
               data => {
              //console.log(data);
              this.dimensionGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}

