import { Component, OnInit } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';

import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
 
@Component({
	selector: 'app-projects',
	templateUrl: './projects.component.html',
	styleUrls: ['./projects.component.css']
})
export class AprojectsComponent implements OnInit { 

	@BlockUI() blockUI: NgBlockUI;

	public currentUser: any;
	public orgId: any;
	public orgInfo: any;
	public projects: any;
	constructor(
		private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title
		) {
		this.blockUI.start('Loading...'); // Start blocking
		this.projects = [];
		this.title.setTitle('Projects List');
	}
 
	ngOnInit() {
		this.currentUser = this.authService.currentUserValue;
		this.orgId = this.currentUser.org_id;
		this.getCurOrganization(this.orgId);
	}

	getCurOrganization(prm){
		this.dataService.getCurOrgDetails(prm).subscribe(
			data => {
				this.orgInfo = data.orgInfo[0];
				this.projects = this.orgInfo.projects;
				console.log(this.orgInfo);
				this.blockUI.stop();
			},
			err => {
				console.log("inside projects list error", JSON.stringify(err.message));
				// alert(JSON.stringify(err.message));
				this.blockUI.stop();
			}
		);
	}

}
