import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';
@Component({
  selector: 'app-questiontype',
  templateUrl: './questiontype.component.html',
  styleUrls: ['./questiontype.component.css']
})
export class QuestiontypeComponent implements OnInit {

  createQuestionTypeForm: FormGroup;
  editQuestionTypeForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public questionTypedata: any;
  public questionTypeEditdata:any
  public currentUser: any;
  public showModal:any;
  public questionTypeData:any;
  public QuestionTypeDetails:any;
  public questionTypeId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  

  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 
      this.blockUI.start('Loading...'); // Start blocking

      this.questionTypedata = {};
    this.questionTypeEditdata={};
    }

  ngOnInit() {
    this.questionTypeGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createQuestionTypeForm = this.formBuilder.group({
  questionType: ['', Validators.required]
 });

 this.editQuestionTypeForm = this.formBuilder.group({
  questionTypeId:[''],
  questionType:['']
 });

  }

  questionTypeFormSubmit() {
		this.submitted = true;
		if (this.createQuestionTypeForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let questionTypeFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.questionTypedata);

		questionTypeFormData.append('user_id', this.currentUser.id);
		questionTypeFormData.append('question_type', this.questionTypedata.questionType);
		

		// console.log(questionTypeFormData);
		this.dataService.createQuestionType(questionTypeFormData).subscribe(
			data => {
      //  console.log(data);
        this.createQuestionTypeForm.reset();
        this.questionTypeGetAllData();
        
			},
			err => {
				console.log("Question Type Creation error", JSON.stringify(err.message));
			}
		);
  }

  questionTypeGetAllData(){
    this.dataService.getAllQuestionType().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.questionTypeData=data.questionType_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();
				}
			},
			err => {
				console.log("Question Type get error", JSON.stringify(err.message));
			}
		);
  }

  questionTypeEdit(QuestionTypeAllData){
    //console.log(QuestionTypeAllData.id);
    this.dataService.getQuestionTypeDetails(QuestionTypeAllData.id).subscribe(
			data => {
    //  console.log(data.QuestionType_details);
      this.QuestionTypeDetails=data.QuestionType_details;

      this.editQuestionTypeForm.patchValue({
        questionTypeId:data.QuestionType_details[0]['id'],
        questionType:data.QuestionType_details[0]['question_types'],
        


      });


				
			}
		);

  }

  questionTypeFormUpdate() {
		this.submitted = true;
		if (this.editQuestionTypeForm.invalid) {
			return;
    }
    
    
    let questionTypeUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
   // console.log(this.questionTypeEditdata);

    questionTypeUpdateFormData.append('user_id', this.currentUser.id);
    questionTypeUpdateFormData.append('question_type_id', this.questionTypeEditdata.questionTypeId);
		questionTypeUpdateFormData.append('question_type', this.questionTypeEditdata.questionType);
		
		// console.log(questionTypeUpdateFormData);
		this.dataService.updateQuestionType(questionTypeUpdateFormData).subscribe(
			data => {
     //   console.log(data);

          this.questionTypeGetAllData();

        


			
			},
			err => {
				console.log("Question Type Creation error", JSON.stringify(err.message));
			}
		);
  }

  questionTypeDelete(QuestionTypeAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteQuestionTypeDetails(QuestionTypeAllData.id).subscribe(
               data => {
             // console.log(data);
              this.questionTypeGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}
