import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';
@Component({
  selector: 'app-levels',
  templateUrl: './levels.component.html',
  styleUrls: ['./levels.component.css']
})
export class LevelsComponent implements OnInit {

 
  createLevelsForm: FormGroup;
  editLevelsForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public levelsdata: any;
  public levelsEditdata:any
  public currentUser: any;
  public showModal:any;
  public levelsData:any;
  public LevelsDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 
      this.blockUI.start('Loading...'); // Start blocking

      this.levelsdata = {};
    this.levelsEditdata={};
    }

  ngOnInit() {
    this.orgGetAllData();

    this.levelsGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createLevelsForm = this.formBuilder.group({
  organization: ['', Validators.required],
  levels:['',Validators.required]
 });

 this.editLevelsForm = this.formBuilder.group({
  levelsId:[''],
  organization:[''],
  levels:['']
 });

  }


  orgGetAllData(){
    this.dataService.getAllOrganization().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.organizationData=data.organization_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Orgnization get error", JSON.stringify(err.message));
			}
		);
  }
  levelsFormSubmit() {
		this.submitted = true;
		if (this.createLevelsForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let levelsFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.levelsdata);

		levelsFormData.append('user_id', this.currentUser.id);
    levelsFormData.append('organization', this.levelsdata.organization);
    levelsFormData.append('levels', this.levelsdata.levels);

		

		// console.log(levelsFormData);
		this.dataService.createLevels(levelsFormData).subscribe(
			data => {
      //  console.log(data);
        this.createLevelsForm.reset();
        this.levelsGetAllData();
        
			},
			err => {
				console.log("Levels Creation error", JSON.stringify(err.message));
			}
		);
  }

  levelsGetAllData(){
    this.dataService.getAllLevels().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.levelsData=data.levels_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();

				}
			},
			err => {
				console.log("Levels get error", JSON.stringify(err.message));
			}
		);
  }

  levelsEdit(LevelsAllData){
   // console.log(LevelsAllData.id);
    this.dataService.getLevelsDetails(LevelsAllData.id).subscribe(
			data => {
     // console.log(data.Levels_details);
      this.LevelsDetails=data.Levels_details;

      this.editLevelsForm.patchValue({
        levelsId:data.Levels_details[0]['id'],
        levels:data.Levels_details[0]['level_cuts'],
        organization:data.Levels_details[0]['org_id'],

        


      });


				
			}
		);

  }

  levelsFormUpdate() {
		this.submitted = true;
		if (this.editLevelsForm.invalid) {
			return;
    }
    
    
    let levelsUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
   // console.log(this.levelsEditdata);

    levelsUpdateFormData.append('user_id', this.currentUser.id);
    levelsUpdateFormData.append('levels_id', this.levelsEditdata.levelsId);
    levelsUpdateFormData.append('organization', this.levelsEditdata.organization);
    levelsUpdateFormData.append('levels', this.levelsEditdata.levels);

		
		// console.log(levelsUpdateFormData);
		this.dataService.updateLevels(levelsUpdateFormData).subscribe(
			data => {
      //  console.log(data);

          this.levelsGetAllData();

        


			
			},
			err => {
				console.log("Levels Creation error", JSON.stringify(err.message));
			}
		);
  }

  levelsDelete(LevelsAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteLevelsDetails(LevelsAllData.id).subscribe(
               data => {
            //  console.log(data);
              this.levelsGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}




