import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ActivatedRoute } from '@angular/router';

import { DataService } from '../../services/data.service';

import Swal from 'sweetalert2';
import {formatDate} from '@angular/common';

@Component({
	selector: 'app-beatssurvey',
	templateUrl: './beatssurvey.component.html',
	styleUrls: ['./beatssurvey.component.css']
})
export class BeatssurveyComponent implements OnInit {
	@BlockUI() blockUI: NgBlockUI;
	@ViewChild('comment') comment : ElementRef;

	public surveyCode: any; 
	public surveyDetails: any;
	public surveyQuestions: any;
	public currentDate: any;
	public linkExpire: any;
	public linkExpiredMessage: any;
	public optionArr: any = [];
	public disableCommentBox: any;
	public allAnsSubmitted: any;
	public finishSurveyView: any;

	constructor(
		private dataService: DataService,
		private route: ActivatedRoute) { 
		this.surveyCode = this.route.snapshot.params.surveyCode;
		this.currentDate = new Date();
		this.currentDate = formatDate(this.currentDate, 'yyyy-MM-dd', 'en');
		this.linkExpire = false;
		this.disableCommentBox = false;
		// this.allAnsSubmitted = true;
		this.finishSurveyView = false;
	}

	ngOnInit() {
		// console.log(this.currentDate);
		this.blockUI.start('Loading...');
		this.dataService.getWeekwiseSurveyQuestionsBySurveyCode(this.surveyCode).subscribe(
			data => {
				console.log(data);
				this.surveyDetails = data.survey_details;
				this.surveyQuestions = data.survey_questions;

				if(this.surveyDetails.status === 'complete') {
					this.finishSurveyView = true;
				}

				for(let ques of this.surveyQuestions) {
					// ques.answer = [];
					ques.selected_answer = '';
					let questionOptions = ques.question_options
					// console.log(questionOptions);
					if(questionOptions !== undefined) {
						for(let quesOpt of questionOptions) {
							quesOpt.selected = false;
						};
					}
				};

				if(this.currentDate >= this.surveyDetails.survey_start_date && this.currentDate <= this.surveyDetails.survey_end_date ) {
					this.linkExpire = false;
					console.log("The date is between start and end date");
				}
				else if(this.currentDate < this.surveyDetails.survey_start_date){
					this.linkExpire = true;
					this.linkExpiredMessage = "Survey will start on "+ this.surveyDetails.survey_start_date;
					console.log("Survey will start on "+ this.surveyDetails.survey_start_date);
				}
				else {
					this.linkExpire = true;
					this.linkExpiredMessage = "This link is expired.";
					console.log("Link expired");
				}
				this.blockUI.stop();
			},
			err => {
				// alert(JSON.stringify(err.message));
				this.blockUI.stop();
			}
		);
	}

	selectOption(options, currentQuestion) {
		// console.log(options, currentQuestion);

		// options.selected = !options.selected;
		// if(options.selected) {
		// 	currentQuestion.answer.push(options.option_value);
		// 	// currentQuestion.answer = options.option_value;
		// }else{
		// 	currentQuestion.answer = currentQuestion.answer.filter(item => item !== options.option_value);
		// 	// currentQuestion.answer = "";
		// }

		currentQuestion.selected_answer = options.option_value;
		// console.log(currentQuestion);
	}

	addComment(comment, currentQuestion) {
		// console.log(comment, currentQuestions);

		// if(comment != '') {
		// 	currentQuestion.answer.push(comment);
		// 	this.disableCommentBox = true;
		// }
		// else {
		// 	Swal.fire('Please enter comment.');
		// }

		currentQuestion.selected_answer = comment;
		// console.log(currentQuestion);
	}

	finishSurvey() {
		// console.log(this.surveyQuestions);
		this.allAnsSubmitted = true;

		for(let question of this.surveyQuestions) {
			if(question.selected_answer == '') {
				Swal.fire('Please attempt all the questions.');
				this.allAnsSubmitted = false;
				return;
			}
		}

		if(this.allAnsSubmitted == true) {
			// console.log('Save Answers.');
			let surveyQuesAnsObj = {
				'survey_code': this.surveyCode,
				'ques_ans': this.surveyQuestions
			};

			this.dataService.saveBeatsSurveyAnswers(surveyQuesAnsObj).subscribe(
				data => {
					console.log(data);
					if(data.code === 200) {
						this.finishSurveyView = true;
					}
					this.blockUI.stop();
				},
				err => {
					this.blockUI.stop();
				}
			);
		}
	}

}
