@Component({
  selector: 'app-likert-options',
  templateUrl: './likert-options.component.html',
  styleUrls: ['./likert-options.component.css']
})
export class LikertOptionsComponent implements OnInit {

 
  createLikertOptionsForm: FormGroup;
  editLikertOptionsForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public likertOptionsdata: any;
  public likertOptionsEditdata:any
  public currentUser: any;
  public showModal:any;
  public likertOptionsData:any;
  public LikertOptionsDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;
  questionTypeData: any;

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 
      this.blockUI.start('Loading...'); // Start blocking

      this.likertOptionsdata = {};
    this.likertOptionsEditdata={};
    }

  ngOnInit() {
    this.questionTypeGetAllData();

    this.likertOptionsGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createLikertOptionsForm = this.formBuilder.group({
  questionType: ['', Validators.required],
  optionValue:['',Validators.required],
  optionLabel:['', Validators.required],
  optionOrder:['', Validators.required],
  optionStatement:['']

 });

 this.editLikertOptionsForm = this.formBuilder.group({
  likertOptionsId:[''],
  questionType: [''],
  optionValue:[''],
  optionLabel:[''],
  optionOrder:[''],
  optionStatement:['']
 });

  }


  questionTypeGetAllData(){
    this.dataService.getAllQuestionType().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.questionTypeData=data.questionType_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Question Type get error", JSON.stringify(err.message));
			}
		);
  }
  likertOptionsFormSubmit() {
		this.submitted = true;
		if (this.createLikertOptionsForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let likertOptionsFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.likertOptionsdata);

		likertOptionsFormData.append('user_id', this.currentUser.id);
    likertOptionsFormData.append('questionType', this.likertOptionsdata.questionType);
    likertOptionsFormData.append('optionValue', this.likertOptionsdata.optionValue);
    likertOptionsFormData.append('optionLabel', this.likertOptionsdata.optionLabel);
    likertOptionsFormData.append('optionOrder', this.likertOptionsdata.optionOrder);
    likertOptionsFormData.append('optionStatement', this.likertOptionsdata.optionStatement);




		

		 //console.log(likertOptionsFormData);
		this.dataService.createLikertOptions(likertOptionsFormData).subscribe(
			data => {
       // console.log(data);
        this.createLikertOptionsForm.reset();
        this.likertOptionsGetAllData();
        
			},
			err => {
				console.log("LikertOptions Creation error", JSON.stringify(err.message));
			}
		);
  }

  likertOptionsGetAllData(){
    this.dataService.getAllLikertOptions().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.likertOptionsData=data.likertOptions_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();
				}
			},
			err => {
				console.log("LikertOptions get error", JSON.stringify(err.message));
			}
		);
  }

  likertOptionsEdit(LikertOptionsAllData){
  //  console.log(LikertOptionsAllData.id);
    this.dataService.getLikertOptionsDetails(LikertOptionsAllData.id).subscribe(
			data => {
     // console.log(data.LikerOptions_details[0]);
      this.LikertOptionsDetails=data.LikertOptions_details;

      this.editLikertOptionsForm.patchValue({
        likertOptionsId:data.LikerOptions_details[0]['id'],
        questionType:data.LikerOptions_details[0]['ques_type_id'],
        optionValue:data.LikerOptions_details[0]['option_value'],
        optionLabel:data.LikerOptions_details[0]['option_label'],
        optionStatement:data.LikerOptions_details[0]['option_statement'],
        optionOrder:data.LikerOptions_details[0]['option_order'],


        


      });


				
			}
		);

  }

  likertOptionsFormUpdate() {
		this.submitted = true;
		if (this.editLikertOptionsForm.invalid) {
			return;
    }
    
    
    let likertOptionsUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
   // console.log(this.likertOptionsEditdata);

    likertOptionsUpdateFormData.append('user_id', this.currentUser.id);
    likertOptionsUpdateFormData.append('likertOptions_id', this.likertOptionsEditdata.likertOptionsId);
    likertOptionsUpdateFormData.append('questionType', this.likertOptionsEditdata.questionType);
    likertOptionsUpdateFormData.append('optionValue', this.likertOptionsEditdata.optionValue);
    likertOptionsUpdateFormData.append('optionLabel', this.likertOptionsEditdata.optionLabel);
    likertOptionsUpdateFormData.append('optionOrder', this.likertOptionsEditdata.optionOrder);
    likertOptionsUpdateFormData.append('optionStatement', this.likertOptionsEditdata.optionStatement);


		
		// console.log(likertOptionsUpdateFormData);
		this.dataService.updateLikertOptions(likertOptionsUpdateFormData).subscribe(
			data => {
     //   console.log(data);

          this.likertOptionsGetAllData();

        


			
			},
			err => {
				console.log("LikertOptions Creation error", JSON.stringify(err.message));
			}
		);
  }

  likertOptionsDelete(LikertOptionsAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteLikertOptionsDetails(LikertOptionsAllData.id).subscribe(
               data => {
            //  console.log(data);
              this.likertOptionsGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}



