	questionFormSubmit(){
		// console.log(this.selectedarray);
		if(this.selectedarray < 32){
        	Swal.fire('Select at least 32 questions' );
	    }else{
	    	if(this.checkOrgLikertCount < 6) {
				Swal.fire("Please select 6 Likert Scale questions from Organization. You have selected "+ this.checkOrgLikertCount +'.');
				return;
			}
			if(this.checkTeamLikertCount < 6) {
				Swal.fire("Please select 6 Likert Scale questions from Team. You have selected "+ this.checkTeamLikertCount +'.');
				return;
			}
			if(this.checkLshipLikertCount < 6) {
				Swal.fire("Please select 6 Likert Scale questions from Leadership. You have selected "+ this.checkLshipLikertCount +'.');
				return;
			}
			if(this.checkJobLikertCount < 6) {
				Swal.fire("Please select 6 Likert Scale questions from Job. You have selected "+ this.checkJobLikertCount +'.');
				return;
			}
			this.selectedOrg = this.selectedOrg.concat(this.selectedTeam);
			this.selectedOrg = this.selectedOrg.concat(this.selectedLship);
			this.selectedOrg = this.selectedOrg.concat(this.selectedJob);
			var questionObj = {
	          project_id: this.lastProjId, 
	          user_id: this.currentUser.id,
	          ques_ids: this.selectedOrg
      		}
      		this.dataService.createQuestionBnk(questionObj).subscribe(
				data => {
					if(data.message == 'Success') {
						this.projectid = data.projectId;
						this.changepanel('empDetails');
					}
				},
				err => {
					console.log("Project Creation error", JSON.stringify(err.message));
				}
			);
		}
	}

	

	getFilterDetails(dt){
		let emptotal = [];
		if(this.selectedAgeDropDown !== 'All' && this.selectedLevelDropDown !== 'All' && this.selectedTenureDropDown !== 'All'){
			this.allFilterEmp = this.allemployees.filter(q => q.age === this.selectedAgeDropDown && q.level === this.selectedLevelDropDown && q.tenure === this.selectedTenureDropDown);
		}else if(this.selectedAgeDropDown !== 'All' && this.selectedLevelDropDown !== 'All' && this.selectedTenureDropDown === 'All'){
			this.allFilterEmp = this.allemployees.filter(q => q.age === this.selectedAgeDropDown && q.level === this.selectedLevelDropDown);
		}else if(this.selectedAgeDropDown !== 'All' && this.selectedLevelDropDown === 'All' && this.selectedTenureDropDown !== 'All'){
			this.allFilterEmp = this.allemployees.filter(q => q.age === this.selectedAgeDropDown && q.tenure === this.selectedTenureDropDown);
		}else if(this.selectedAgeDropDown === 'All' && this.selectedLevelDropDown !== 'All' && this.selectedTenureDropDown !== 'All'){
			this.allFilterEmp = this.allemployees.filter(q => q.level === this.selectedLevelDropDown && q.tenure === this.selectedTenureDropDown);
		}else if(this.selectedAgeDropDown !== 'All' && this.selectedLevelDropDown === 'All' && this.selectedTenureDropDown === 'All'){
			this.allFilterEmp = this.allemployees.filter(q => q.age === this.selectedAgeDropDown);
		}else if(this.selectedAgeDropDown === 'All' && this.selectedLevelDropDown !== 'All' && this.selectedTenureDropDown === 'All'){
			this.allFilterEmp = this.allemployees.filter(q => q.level === this.selectedLevelDropDown);
		}else if(this.selectedAgeDropDown === 'All' && this.selectedLevelDropDown === 'All' && this.selectedTenureDropDown !== 'All'){
			this.allFilterEmp = this.allemployees.filter(q => q.tenure === this.selectedTenureDropDown);
		}else if(this.selectedAgeDropDown === 'All' && this.selectedLevelDropDown === 'All' && this.selectedTenureDropDown === 'All'){
			this.allFilterEmp = this.allemployees;
		}
		// console.log(this.allFilterEmp);

	}

	employeeFormSubmit(){

		// console.log(this.allFilterEmp);
		if(this.allFilterEmp.length > 0){
			let arr = [];
			for(let employee of this.allFilterEmp) {
	           this.empIds.push(employee.id);
	       	};
	       	
			Swal.fire({
			  title: 'Are you sure?',
			  showCancelButton: true,
			  confirmButtonColor: '#3085d6',
			  cancelButtonColor: '#d33',
			  confirmButtonText: 'Yes'
			}).then((result) => {
			  if (result.value) {
			  	var employeeObj = {
		          project_id: this.lastProjId, 
		          user_id: this.currentUser.id,
			  		emp_ids : this.empIds
			  	}
			  	this.dataService.addProjEmp(employeeObj).subscribe(
					data => {
						// console.log(data);
						if(data.message == 'Success') {
							this.router.navigate(['admin/projects/']);
						}
					},
					err => {
						console.log("Project Creation error", JSON.stringify(err.message));
					}
				);
	      		// console.log(employeeObj);
			  }
			})
		} else{
			Swal.fire("Please select employees for survey");
		}
	}

