	likertQuesSave(){
		console.log(this.selectedDimension.length);
		console.log(this.selectedSubDimension.length);
		console.log(this.quesdata.likertquestxt.length);

		if(this.selectedDimension.length > 0 && this.selectedSubDimension.length > 0 && this.quesdata.likertquestxt.length > 0){
			var quesdata = {
				questionText: this.quesdata.likertquestxt,
				questionType: this.allFilterData.questiontype[0].id,
				dimensionId:this.selectedDimension,
				subdimensionId:this.selectedSubDimension,
				createdby: this.currentUser.id,
				org_id: this.orgId
			}
			this.dataService.addLikertScale(quesdata).subscribe(
	        data => {
	            if(data.code === 200) {
	            	this.getAllQuestions();
	            	this.hideAddLikertQuestionPopup();
					Swal.fire('Question data inserted');	
	                this.blockUI.stop();
	             }   
	             else {
					Swal.fire('Question data not inserted' );	
	             }
	             this.blockUI.stop();
	        },
	        err => {
	            //this.surveyView = 'aerror';
					Swal.fire('Question data not inserted' );	
	                this.blockUI.stop();
	        });
		}else{
			Swal.fire('Please fill all the fields' );	
		}
	}

