	mcqQuesSave() {
		// console.log(this.optionFieldArray);

		if(this.selectedDimension.length > 0 && this.selectedSubDimension.length > 0 && this.quesdata.mcqquestxt.length > 0){
			this.quesOptions = [];
			for(let opt of this.optionFieldArray) {
				if(opt.optionText != '') {
					this.quesOptions.push(opt.optionText);
				}
			}

			// console.log(addedOptionCount);
			if(this.quesOptions.length <= 1) {
				Swal.fire('Please add atleast two options');
				return;
			}
			else {
				var quesdata = {
					questionText: this.quesdata.mcqquestxt,
					questionType: this.allFilterData.questiontype[1].id,
					dimensionId:this.selectedDimension,
					subdimensionId:this.selectedSubDimension,
					createdby: this.currentUser.id,
					quesOptions: this.quesOptions,
					org_id: this.orgId
				}

				console.log(quesdata);

				this.dataService.addMcq(quesdata).subscribe(
					data => {
						if(data.code === 200) {
							this.getAllQuestions();
							this.hideAddMcqQuestionPopup();
							Swal.fire('Question data inserted');	
							this.blockUI.stop();
						}   
						else {
							Swal.fire('Question data not inserted' );	
						}
						this.blockUI.stop();
					},
					err => {
						//this.surveyView = 'aerror';
						Swal.fire('Question data not inserted' );	
						this.blockUI.stop();
					}
				);
			}

		} else {
			Swal.fire('Please fill all the fields' );
		}
	}

