import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';

@Component({
  selector: 'app-employee-event',
  templateUrl: './employee-event.component.html',
  styleUrls: ['./employee-event.component.css']
})
export class EmployeeEventComponent implements OnInit {

 
  createEmployeeEventForm: FormGroup;
  editEmployeeEventForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public employeeEventdata: any;
  public employeeEventEditdata:any
  public currentUser: any;
  public showModal:any;
  public employeeEventData:any;
  public EmployeeEventDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 
      this.blockUI.start('Loading...'); // Start blocking

      this.employeeEventdata = {};
    this.employeeEventEditdata={};
    }

  ngOnInit() {
    this.orgGetAllData();

    this.employeeEventGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createEmployeeEventForm = this.formBuilder.group({
  organization: ['', Validators.required],
  employeeEvent:['',Validators.required]
 });

 this.editEmployeeEventForm = this.formBuilder.group({
  employeeEventId:[''],
  organization:[''],
  employeeEvent:['']
 });

  }


  orgGetAllData(){
    this.dataService.getAllOrganization().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.organizationData=data.organization_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Orgnization get error", JSON.stringify(err.message));
			}
		);
  }
  employeeEventFormSubmit() {
		this.submitted = true;
		if (this.createEmployeeEventForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let employeeEventFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.employeeEventdata);

		employeeEventFormData.append('user_id', this.currentUser.id);
    employeeEventFormData.append('organization', this.employeeEventdata.organization);
    employeeEventFormData.append('employeeEvent', this.employeeEventdata.employeeEvent);

		

		// console.log(employeeEventFormData);
		this.dataService.createEmployeeEvent(employeeEventFormData).subscribe(
			data => {
       // console.log(data);
        this.createEmployeeEventForm.reset();
        this.employeeEventGetAllData();
        
			},
			err => {
				console.log("EmployeeEvent Creation error", JSON.stringify(err.message));
			}
		);
  }

  employeeEventGetAllData(){
    this.dataService.getAllEmployeeEvent().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.employeeEventData=data.employeeEvent_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();

				}
			},
			err => {
				console.log("EmployeeEvent get error", JSON.stringify(err.message));
			}
		);
  }

  employeeEventEdit(EmployeeEventAllData){
    //console.log(EmployeeEventAllData.id);
    this.dataService.getEmployeeEventDetails(EmployeeEventAllData.id).subscribe(
			data => {
      //console.log(data.EmployeeEvent_details);
      this.EmployeeEventDetails=data.EmployeeEvent_details;

      this.editEmployeeEventForm.patchValue({
        employeeEventId:data.EmployeeEvent_details[0]['id'],
        employeeEvent:data.EmployeeEvent_details[0]['event'],
        organization:data.EmployeeEvent_details[0]['org_id'],

        


      });


				
			}
		);

  }

  employeeEventFormUpdate() {
		this.submitted = true;
		if (this.editEmployeeEventForm.invalid) {
			return;
    }
    
    
    let employeeEventUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.employeeEventEditdata);

    employeeEventUpdateFormData.append('user_id', this.currentUser.id);
    employeeEventUpdateFormData.append('employeeEvent_id', this.employeeEventEditdata.employeeEventId);
    employeeEventUpdateFormData.append('organization', this.employeeEventEditdata.organization);
    employeeEventUpdateFormData.append('employeeEvent', this.employeeEventEditdata.employeeEvent);

		
		//// console.log(employeeEventUpdateFormData);
		this.dataService.updateEmployeeEvent(employeeEventUpdateFormData).subscribe(
			data => {
      //  console.log(data);

          this.employeeEventGetAllData();

        


			
			},
			err => {
				console.log("EmployeeEvent Creation error", JSON.stringify(err.message));
			}
		);
  }

  employeeEventDelete(EmployeeEventAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteEmployeeEventDetails(EmployeeEventAllData.id).subscribe(
               data => {
             // console.log(data);
              this.employeeEventGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}




