import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';

@Component({
  selector: 'app-performance',
  templateUrl: './performance.component.html',
  styleUrls: ['./performance.component.css']
})
export class PerformanceComponent implements OnInit {

 
  createPerformanceForm: FormGroup;
  editPerformanceForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public performancedata: any;
  public performanceEditdata:any
  public currentUser: any;
  public showModal:any;
  public performanceData:any;
  public PerformanceDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 
      this.blockUI.start('Loading...'); // Start blocking

      this.performancedata = {};
    this.performanceEditdata={};
    }

  ngOnInit() {
    this.orgGetAllData();

    this.performanceGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createPerformanceForm = this.formBuilder.group({
  organization: ['', Validators.required],
  performance:['',Validators.required]
 });

 this.editPerformanceForm = this.formBuilder.group({
  performanceId:[''],
  organization:[''],
  performance:['']
 });

  }


  orgGetAllData(){
    this.dataService.getAllOrganization().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.organizationData=data.organization_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Orgnization get error", JSON.stringify(err.message));
			}
		);
  }
  performanceFormSubmit() {
		this.submitted = true;
		if (this.createPerformanceForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let performanceFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
  //  console.log(this.performancedata);

		performanceFormData.append('user_id', this.currentUser.id);
    performanceFormData.append('organization', this.performancedata.organization);
    performanceFormData.append('performance', this.performancedata.performance);

		

		 //console.log(performanceFormData);
		this.dataService.createPerformance(performanceFormData).subscribe(
			data => {
        //console.log(data);

        this.createPerformanceForm.reset();
        this.performanceGetAllData();
        
			},
			err => {
				console.log("Performance Creation error", JSON.stringify(err.message));
			}
		);
  }

  performanceGetAllData(){
    this.dataService.getAllPerformance().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.performanceData=data.performance_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();

				}
			},
			err => {
				console.log("Performance get error", JSON.stringify(err.message));
			}
		);
  }

  performanceEdit(PerformanceAllData){
    //console.log(PerformanceAllData.id);
    this.dataService.getPerformanceDetails(PerformanceAllData.id).subscribe(
			data => {
     // console.log(data.Performance_details);
      this.PerformanceDetails=data.Performance_details;

      this.editPerformanceForm.patchValue({
        performanceId:data.Performance_details[0]['id'],
        performance:data.Performance_details[0]['performance'],
        organization:data.Performance_details[0]['org_id'],

        


      });


				
			}
		);

  }

  performanceFormUpdate() {
		this.submitted = true;
		if (this.editPerformanceForm.invalid) {
			return;
    }
    
    
    let performanceUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
   // console.log(this.performanceEditdata);

    performanceUpdateFormData.append('user_id', this.currentUser.id);
    performanceUpdateFormData.append('performance_id', this.performanceEditdata.performanceId);
    performanceUpdateFormData.append('organization', this.performanceEditdata.organization);
    performanceUpdateFormData.append('performance', this.performanceEditdata.performance);

		
		// console.log(performanceUpdateFormData);
		this.dataService.updatePerformance(performanceUpdateFormData).subscribe(
			data => {
      //  console.log(data);

          this.performanceGetAllData();

        


			
			},
			err => {
				console.log("Performance Creation error", JSON.stringify(err.message));
			}
		);
  }

  performanceDelete(PerformanceAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deletePerformanceDetails(PerformanceAllData.id).subscribe(
               data => {
            //  console.log(data);
              this.performanceGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}



