import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';

@Component({
  selector: 'app-business-unit',
  templateUrl: './business-unit.component.html',
  styleUrls: ['./business-unit.component.css']
})
export class BusinessUnitComponent implements OnInit {

 
  createBusinessUnitForm: FormGroup;
  editBusinessUnitForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public businessUnitdata: any;
  public businessUnitEditdata:any
  public currentUser: any;
  public showModal:any;
  public businessUnitData:any;
  public BusinessUnitDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 

      this.blockUI.start('Loading...'); // Start blocking


      this.businessUnitdata = {};
    this.businessUnitEditdata={};
    }

  ngOnInit() {
    this.orgGetAllData();

    this.businessUnitGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createBusinessUnitForm = this.formBuilder.group({
  organization: ['', Validators.required],
  businessUnit:['',Validators.required]
 });

 this.editBusinessUnitForm = this.formBuilder.group({
  businessUnitId:[''],
  organization:[''],
  businessUnit:['']
 });

  }


  orgGetAllData(){
    this.dataService.getAllOrganization().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.organizationData=data.organization_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Orgnization get error", JSON.stringify(err.message));
			}
		);
  }
  businessUnitFormSubmit() {
		this.submitted = true;
		if (this.createBusinessUnitForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let businessUnitFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.businessUnitdata);

		businessUnitFormData.append('user_id', this.currentUser.id);
    businessUnitFormData.append('organization', this.businessUnitdata.organization);
    businessUnitFormData.append('businessUnit', this.businessUnitdata.businessUnit);

		

		// console.log(businessUnitFormData);
		this.dataService.createBusinessUnit(businessUnitFormData).subscribe(
			data => {
        //console.log(data);
        this.createBusinessUnitForm.reset();
        this.businessUnitGetAllData();
        
			},
			err => {
				console.log("BusinessUnit Creation error", JSON.stringify(err.message));
			}
		);
  }

  businessUnitGetAllData(){
    this.dataService.getAllBusinessUnit().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
      // console.log(data);
        this.businessUnitData=data.businessUnit_details;
        //	this.changepanel('queDetails');

        this.blockUI.stop();

				}
			},
			err => {
				console.log("BusinessUnit get error", JSON.stringify(err.message));
			}
		);
  }

  businessUnitEdit(BusinessUnitAllData){
   // console.log(BusinessUnitAllData.id);
    this.dataService.getBusinessUnitDetails(BusinessUnitAllData.id).subscribe(
			data => {
      //console.log(data.BusinessUnit_details);
      this.BusinessUnitDetails=data.BusinessUnit_details;

      this.editBusinessUnitForm.patchValue({
        businessUnitId:data.BusinessUnit_details[0]['id'],
        businessUnit:data.BusinessUnit_details[0]['business_unit'],
        organization:data.BusinessUnit_details[0]['org_id'],

        


      });


				
			}
		);

  }

  businessUnitFormUpdate() {
		this.submitted = true;
		if (this.editBusinessUnitForm.invalid) {
			return;
    }
    
    
    let businessUnitUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
   // console.log(this.businessUnitEditdata);

    businessUnitUpdateFormData.append('user_id', this.currentUser.id);
    businessUnitUpdateFormData.append('businessUnit_id', this.businessUnitEditdata.businessUnitId);
    businessUnitUpdateFormData.append('organization', this.businessUnitEditdata.organization);
    businessUnitUpdateFormData.append('businessUnit', this.businessUnitEditdata.businessUnit);

		
		// console.log(businessUnitUpdateFormData);
		this.dataService.updateBusinessUnit(businessUnitUpdateFormData).subscribe(
			data => {
       // console.log(data);

          this.businessUnitGetAllData();

        


			
			},
			err => {
				console.log("BusinessUnit Creation error", JSON.stringify(err.message));
			}
		);
  }

  businessUnitDelete(BusinessUnitAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteBusinessUnitDetails(BusinessUnitAllData.id).subscribe(
               data => {
              //console.log(data);
              this.businessUnitGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}




