    OrganizationComponent,
    DlregisterComponent,
    LregisterComponent,
    AddprojectComponent,
    BeatssurveyComponent,
    MCQComponent,
    BeatssurveychatComponent,
    BeatsdashboardComponent,
    AdmindashboardComponent,
    AdminorganizationComponent,
    OpenSpaceComponent,
    HeaderdashboardComponent,
    QuestiontypeComponent,
    DimensionComponent,
    SubdimensionComponent,
    TenureComponent,
    PerformanceComponent,
