  mcqFormSubmit() {
		this.submitted = true;
		if (this.createMcqForm.invalid) {
			return;
    }
    this.quesOptions = [];
    for(let opt of this.optionFieldArray) {
      if(opt.optionText != '') {
       this.quesOptions.push(opt.optionText);
      //  console.log(opt.optionText);
      }
    }
    //console.log('hii');
    //console.log(this.quesOptions);

    // console.log(addedOptionCount);
    if(this.quesOptions.length <= 1) {
      Swal.fire('Please add atleast two options');
      return;
    }
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let mcqFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
  //  console.log(this.mcqdata);

		mcqFormData.append('user_id', this.currentUser.id);
    mcqFormData.append('organization', this.mcqdata.organization);
    mcqFormData.append('dimension', this.mcqdata.dimension);
    mcqFormData.append('subDimension', this.mcqdata.subDimension);
    mcqFormData.append('questionText', this.mcqdata.questionText);
    mcqFormData.append('optionText',this.quesOptions);



		

		 //console.log(mcqFormData);
		this.dataService.createMcq(mcqFormData).subscribe(
			data => {
       // console.log(data);
        this.createMcqForm.reset();
       this.mcqGetAllData();
        
			},
			err => {
				console.log("Mcq Creation error", JSON.stringify(err.message));
			}
		);
  }

  
