import 'datatables.net';
@Component({
  selector: 'app-open-space',
  templateUrl: './open-space.component.html',
  styleUrls: ['./open-space.component.css']
})
export class OpenSpaceComponent implements OnInit {

  
 
  createOpenSpaceForm: FormGroup;
  editOpenSpaceForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public openSpacedata: any;
  public openSpaceEditdata:any
  public currentUser: any;
  public showModal:any;
  public openSpaceData:any;
  public OpenSpaceDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;
  SubDimensionData: any;
  subDimensionData: any;

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 
      this.blockUI.start('Loading...'); // Start blocking

      this.openSpacedata = {};
    this.openSpaceEditdata={};
    }

  ngOnInit() {
    this.openSpaceGetAllData();
    this.orgGetAllData();
    this.dimensionGetAllData();

  //  this.openSpaceGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createOpenSpaceForm = this.formBuilder.group({
  organization: ['', Validators.required],
  dimension:['',Validators.required],
  subDimension:['',Validators.required],
  questionText:['', Validators.required]
 });

 this.editOpenSpaceForm = this.formBuilder.group({
  openSpaceId:[''],
  organization:[''],
  dimension:[''],
  subDimension:[''],
  questionText:['']
 });

  }


  orgGetAllData(){
    this.dataService.getAllOrganization().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.organizationData=data.organization_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Orgnization get error", JSON.stringify(err.message));
			}
		);
  }

  dimensionGetAllData(){
    this.dataService.getAllDimension().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
      //  console.log(data.dimension_details);
        this.dimensionData=data.dimension_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Dimension get error", JSON.stringify(err.message));
			}
		);
  }

  getSudDimension(){
    //console.log(this.openSpacedata.dimension);

    this.dataService.getSubDimensionDetailsAll(this.openSpacedata.dimension).subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
        //console.log(data.subDimension_details);
        this.subDimensionData=data.subDimension_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Sub Dimension get error", JSON.stringify(err.message));
			}
    );
    
   // console.log(this.openSpaceEditdata.dimension);
    this.dataService.getSubDimensionDetailsAll(this.openSpaceEditdata.dimension).subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data.subDimension_details);
        this.subDimensionData=data.subDimension_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Sub Dimension get error", JSON.stringify(err.message));
			}
    );

  }



  openSpaceFormSubmit() {
		this.submitted = true;
		if (this.createOpenSpaceForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let openSpaceFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
   // console.log(this.openSpacedata);

		openSpaceFormData.append('user_id', this.currentUser.id);
    openSpaceFormData.append('organization', this.openSpacedata.organization);
    openSpaceFormData.append('dimension', this.openSpacedata.dimension);
    openSpaceFormData.append('subDimension', this.openSpacedata.subDimension);
    openSpaceFormData.append('questionText', this.openSpacedata.questionText);


		

		// console.log(openSpaceFormData);
		this.dataService.createOpenSpace(openSpaceFormData).subscribe(
			data => {
      //  console.log(data);
        this.createOpenSpaceForm.reset();
       this.openSpaceGetAllData();
        
			},
			err => {
				console.log("OpenSpace Creation error", JSON.stringify(err.message));
			}
		);
  }

  
  openSpaceGetAllData(){
    this.dataService.getAllOpenSpace().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.openSpaceData=data.openSpace_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        this.blockUI.stop();
				}
			},
			err => {
				console.log("Likert Scale get error", JSON.stringify(err.message));
			}
		);
  }

  openSpaceEdit(OpenSpaceAllData){
   // console.log(OpenSpaceAllData.id);
    this.dataService.getOpenSpaceDetails(OpenSpaceAllData.id).subscribe(
			data => {
    //  console.log(data.OpenSpace_details);
      this.OpenSpaceDetails=data.OpenSpace_details;


    
        this.dataService.getSubDimensionDetailsAll(data.OpenSpace_details[0]['dimension_id']).subscribe(
          data => {
          
            if(data.message == 'Success') {
            //	this.lastProjId = data.project_id;
           // console.log(data.subDimension_details);
            this.subDimensionData=data.subDimension_details;
            //	this.changepanel('queDetails');
             //this.router.navigate(['/Aorganization']);
            
            }
          },
          err => {
            console.log("Sub Dimension get error", JSON.stringify(err.message));
          }
        );
      



      this.editOpenSpaceForm.patchValue({
        openSpaceId:data.OpenSpace_details[0]['id'],
        organization:data.OpenSpace_details[0]['org_id'],
        dimension:data.OpenSpace_details[0]['dimension_id'],
        subDimension:data.OpenSpace_details[0]['sub_dimension_id'],
        questionText:data.OpenSpace_details[0]['question_text']
      });


				
			}
    );
    


  }

  openSpaceFormUpdate() {
		this.submitted = true;
		if (this.editOpenSpaceForm.invalid) {
			return;
    }
    
    
    let openSpaceUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.openSpaceEditdata);

    openSpaceUpdateFormData.append('user_id', this.currentUser.id);
    openSpaceUpdateFormData.append('openSpaceId', this.openSpaceEditdata.openSpaceId);
    openSpaceUpdateFormData.append('organization', this.openSpaceEditdata.organization);
    openSpaceUpdateFormData.append('dimension', this.openSpaceEditdata.dimension);
    openSpaceUpdateFormData.append('subDimension', this.openSpaceEditdata.subDimension);
    openSpaceUpdateFormData.append('questionText', this.openSpaceEditdata.questionText);



		
		// console.log(openSpaceUpdateFormData);
		this.dataService.updateOpenSpace(openSpaceUpdateFormData).subscribe(
			data => {
        //console.log(data);

          this.openSpaceGetAllData();

        


			
			},
			err => {
				console.log("OpenSpace Creation error", JSON.stringify(err.message));
			}
		);
  }

  openSpaceDelete(OpenSpaceAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteOpenSpaceDetails(OpenSpaceAllData.id).subscribe(
               data => {
              //console.log(data);
              this.openSpaceGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}



