import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators ,ReactiveFormsModule} from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';

@Component({
  selector: 'app-age',
  templateUrl: './age.component.html',
  styleUrls: ['./age.component.css']
})
export class AgeComponent implements OnInit {

 
  createAgeForm: FormGroup;
  editAgeForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public agedata: any;
  public ageEditdata:any
  public currentUser: any;
  public showModal:any;
  public ageData:any;
  public AgeDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;
  organization="";

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
    private formBuilder: FormBuilder,
    ) { 
      this.blockUI.start('Loading...'); // Start blocking


      this.agedata = {};
    this.ageEditdata={};
    }

  ngOnInit() {
    this.orgGetAllData();

    this.ageGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;
 
 // console.log(this.currentUser);
 this.createAgeForm = this.formBuilder.group({
  organization: ['', Validators.required],
  age:['',Validators.required]
 });

 this.editAgeForm = this.formBuilder.group({
  ageId:[''],
  organization:[''],
  age:['']
 });


  }


  orgGetAllData(){
    this.dataService.getAllOrganization().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.organizationData=data.organization_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Orgnization get error", JSON.stringify(err.message));
			}
		);
  }
  ageFormSubmit() {
		this.submitted = true;
		if (this.createAgeForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let ageFormData: FormData = new FormData();
    
   
    

		ageFormData.append('user_id', this.currentUser.id);
    ageFormData.append('organization', this.agedata.organization);
    ageFormData.append('age', this.agedata.age);

		

		
		this.dataService.createAge(ageFormData).subscribe(
			data => {
     
        this.createAgeForm.reset();

        this.ageGetAllData();
      
       
			},
			err => {
				console.log("Age Creation error", JSON.stringify(err.message));
			}
		);
  }

  ageGetAllData(){
    this.dataService.getAllAge().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.ageData=data.age_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();
				}
			},
			err => {
				console.log("Age get error", JSON.stringify(err.message));
			}
		);
  }

  ageEdit(AgeAllData){
   // console.log(AgeAllData.id);
    this.dataService.getAgeDetails(AgeAllData.id).subscribe(
			data => {
     // console.log(data.Age_details);
      this.AgeDetails=data.Age_details;

      this.editAgeForm.patchValue({
        ageId:data.Age_details[0]['id'],
        age:data.Age_details[0]['age_cuts'],
        organization:data.Age_details[0]['org_id'],

        


      });


				
			}
		);

  }

  ageFormUpdate() {
		this.submitted = true;
		if (this.editAgeForm.invalid) {
			return;
    }
    
    
    let ageUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
  //  console.log(this.ageEditdata);

    ageUpdateFormData.append('user_id', this.currentUser.id);
    ageUpdateFormData.append('age_id', this.ageEditdata.ageId);
    ageUpdateFormData.append('organization', this.ageEditdata.organization);
    ageUpdateFormData.append('age', this.ageEditdata.age);

		
		// console.log(ageUpdateFormData);
		this.dataService.updateAge(ageUpdateFormData).subscribe(
			data => {
      //  console.log(data);

          this.ageGetAllData();

        


			
			},
			err => {
				console.log("Age Creation error", JSON.stringify(err.message));
			}
		);
  }

  ageDelete(AgeAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteAgeDetails(AgeAllData.id).subscribe(
               data => {
            //  console.log(data);
              this.ageGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}



