import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { Title } from '@angular/platform-browser';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
declare var ldBar: any;
declare var require: any;
var Highcharts = require('highcharts/highcharts.js');

@Component({
  selector: 'app-admindashboard',
  templateUrl: './admindashboard.component.html',
  styleUrls: ['./admindashboard.component.css']
})
export class AdmindashboardComponent implements OnInit {
	@BlockUI() blockUI: NgBlockUI;
	public currentUser: any;
	public orgId: any;
	public orgInfo: any;
	public employees: any;
	public projects: any;
	public questions: any;
	public filterdata: any;
  constructor(
  		private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title
  	) { 
  		this.blockUI.start('Loading...'); // Start blocking
		this.title.setTitle('Master Admin');
		this.filterdata = 'all';
  }

  ngOnInit() {

  	this.getOrganizations(this.filterdata);
  	// this.orgGraph();

    // for dropdown mwnu
    $('.dropdown-menu a.dropdown-toggle').on('click', function(e) {
      if (!$(this).next().hasClass('show')) {
        $(this).parents('.dropdown-menu').first().find('.show').removeClass("show");
      }
      var $subMenu = $(this).next(".dropdown-menu");
      $subMenu.toggleClass('show');
    
    
      $(this).parents('li.nav-item.dropdown.show').on('hidden.bs.dropdown', function(e) {
        $('.dropdown-submenu .show').removeClass("show");
      });
    
    
      return false;
    });


  }

  getOrganizations(prm){
  	this.dataService.getAllOrgList(prm).subscribe(
			data => {
				console.log(data);
				this.orgInfo = data.orgInfo;
				this.employees = 0;
				this.projects = 0;
				this.questions = 0;
				for (var i = 0; i < this.orgInfo.length; i++) {
					this.employees += this.orgInfo[i].employees.length;
					this.projects += this.orgInfo[i].projects.length;
					this.questions += this.orgInfo[i].questions.length;
				}
				var org_series = []; var orgemp_series = []; var orgques_series = [];
				for(let org of this.orgInfo) {
                    org_series.push([org.org_name, Number(org.projects.length)]);
                    orgemp_series.push([org.org_name, Number(org.employees.length)]);
                    orgques_series.push([org.org_name, Number(org.questions.length)]);
              	}
              	var clr1 = [] = ['#333', '#CB2326', '#DDDF00', '#24CBE5', '#64E572', '#FF9655', '#CB2326', '#6AF9C4'];
              	var clr2 = [] = ['#50B432', '#ED561B','#DDDF00','#24CBE5','#64E572','#FF9655','#FFF263','#6AF9C4'];
              	var clr3 = [] = ['red', 'green', '#ED561B', '#DDDF00', '#24CBE5', '#64E572', '#FF9655', '#FFF263', '#6AF9C4'];
              	this.orgGraph('orgProchart',org_series,'Project',clr1);
              	this.orgGraph('orgEmpchart',orgemp_series,'Employees',clr2);
              	this.orgGraph('orgquechart',orgques_series,'Questions',clr3);
				this.blockUI.stop();
			},
			err => {
				console.log("inside projects list error", JSON.stringify(err.message));
				// alert(JSON.stringify(err.message));
				this.blockUI.stop();
			}
		);
  }

  orgGraph(container,series,relatd_to,crl){
  	Highcharts.chart(container, {
	    chart: {
	        backgroundColor: 'transparent',
                // margin: [0, 0, 50, 0],
                plotBackgroundColor: null,
                plotBorderWidth: null,
                plotShadow: false,
                width: 330,
                margin: [0, 0, 0, 0],
        spacingTop: 0,
        spacingBottom: 0,
        spacingLeft: 0,
        spacingRight: 0,
                style: {
                    fontFamily: "'segoeui', sans-serif"
                },
	        type: 'pie'
	    },
	    title: {
	        text: ''
	    },
	    'credits': { enabled: false },
        subtitle: {
            text: ''
        },
	    tooltip: {
	         formatter: function () {
            return 'Total No. Of '+ relatd_to +': <b>' + this.y + '</b>';
        }
	    },
	    accessibility: {
	        point: {
	            valueSuffix: '%'
	        }
	    },
	    plotOptions: {
	        pie: {
	        	colors: crl,
	        	size:'60%',
	            allowPointSelect: true,
	            cursor: 'pointer',
	            dataLabels: {
	                enabled: true
	            },
	            showInLegend: false
	        }
	    },
	    series: [{
	        name: '',
	        colorByPoint: true,
	        data: series
	    }]
	});
  }
  
}
