import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ActivatedRoute } from '@angular/router';
import { Title } from '@angular/platform-browser';
import { Options } from 'ng5-slider';
import { DataService } from '../../services/data.service';
import {formatDate} from '@angular/common';
import * as $ from 'jquery';
import Sortable from '../../../assets/Sortable/Sortable';

$.support.touch = 'ontouchend' in document;
@Component({
  selector: 'app-beatssurveychat',
  templateUrl: './beatssurveychat.component.html',
  styleUrls: ['./beatssurveychat.component.css']
})
export class BeatssurveychatComponent implements OnInit {
	@BlockUI() blockUI: NgBlockUI;
    @ViewChild('auto') auto;
    public surveyCode: any;
    public survey: any;
    public surveyView: any;
    public currentQuestion: any;
    public currentQuestionIndex: any;
    public userAnswers: any;
    public surveyError: any;
    public currentYear: any;
    public currentDate: any;
    public formdata: any;
	  public linkExpire: any;
	  public linkExpiredMessage: any;
    public surveyCompletionMessage: any;
	  public selecteditm: any;
    public ostxt_lenghth:boolean; 
    public loaderimg: boolean;
    public loaderimg2: boolean;

    public lastquecheck: boolean;
    public scrollingElement: any;
    public finishSurveyView : boolean;
    public surveystatus: any;
    public screentoggle: boolean;
  constructor(
  	private dataService: DataService,
    private route: ActivatedRoute,
    private title: Title
  	) { 
  		this.blockUI.start('Loading...'); // Start blocking
    this.surveyView = '-';
    this.surveyError =  '';
    this.formdata = {};
    this.formdata.opnspc_txtbx = '';
    this.surveyCode = this.route.snapshot.params.surveyCode;
		this.currentDate = new Date();
		this.currentDate = formatDate(this.currentDate, 'yyyy-MM-dd', 'en');
		this.linkExpire = false;
		this.userAnswers = [];
		this.selecteditm = '';
    this.ostxt_lenghth = false;
    this.loaderimg = false;
    this.loaderimg2 = false;
    this.lastquecheck = false;
    this.screentoggle = false;
    this.surveystatus = false;
  }
 
  ngOnInit() {
 
     if(this.surveyCode !== undefined && this.surveyCode !== '') {
        this.dataService.getWeekwiseSurveyQuestionsBySurveyCode(this.surveyCode).subscribe(
            data => {
				console.log(data);
				        this.survey = data.survey_details;
				        this.survey.questions = data.survey_questions;
               	this.title.setTitle('Beats Survey');
               	// console.log(this.survey);
                 if(this.survey.status === 'complete') {
                    this.surveystatus = true;
                    // console.log(this.surveystatus);
                    this.surveyCompletionMessage = "Survey is compled on ";
                  }
                for(let qobj of this.survey.questions) {
                   if(qobj.question_options !== undefined && qobj.question_options.length > 0) {
                       for(let opt of qobj.question_options) {
                           opt.selected = false;
                       };
                   }
                };
                // console.log(this.survey.questions);
                this.currentQuestionIndex = 0;
                this.startSurvey();
                if(this.currentDate >= this.survey.survey_start_date && this.currentDate <= this.survey.survey_end_date ) {
                  this.linkExpire = false;
                  console.log("The date is between start and end date");
                }
                else if(this.currentDate < this.survey.survey_start_date){
                  this.linkExpire = true;
                  this.linkExpiredMessage = "Survey will start on "+ this.survey.survey_start_date;
                  console.log("Survey will start on "+ this.survey.survey_start_date);
                }
                else if(this.currentDate > this.survey.survey_end_date){
                  this.linkExpire = true;
                  this.linkExpiredMessage = "This link is expired on "+ this.survey.survey_end_date;
                  // console.log("Link expired");
                }
                this.blockUI.stop();
            },
            err => {
                this.surveyView = 'error';
                this.surveyError = 'Survey Code not valid';
                    this.blockUI.stop();
            });
	    }
	    else {
	        this.surveyView = 'error';
	        this.surveyError = 'Survey Code not valid';
	        this.blockUI.stop();
	    }
	  }
	  startSurvey() {
	    this.surveyView = 'survey';
	    this.loadQuestion();
	  }
    changeToggleflag(){
      this.screentoggle = !this.screentoggle;
      if(this.screentoggle === true){
        $("#mainScreen").animate({marginTop: "90vh"});
        $("#scrollstyle").hide(600);
        $("#footerscreen").hide(600);
      }
       if(this.screentoggle === false){
        $("#mainScreen").animate({marginTop: "21vh"});
        $("#scrollstyle").show(600);
        $("#footerscreen").show(600);
      }
    }
 	 loadQuestion() {

      // var promise = new Promise(function(resolve, reject) {
      //   setTimeout(function() {
      //     resolve('I promise to return this after 1 second!');
      //   }, 1000);
      // });
      // promise.then(function(value) {
      //   console.log(value);
      // });



      if(this.currentQuestionIndex != '0'){
          this.loaderimg = true;
          this.loaderimg2 = true;
        setTimeout(()=>{
          this.loaderimg2 = false;
        },500);
        setTimeout(()=>{
          this.loaderimg = false;
        },1500);
      }
      this.currentQuestion = this.survey.questions[this.currentQuestionIndex];
      if(this.userAnswers[this.currentQuestionIndex] === undefined) {
	        this.userAnswers[this.currentQuestionIndex] = {
	            questionId: this.currentQuestion.question_id,
	            questionType: this.currentQuestion.question_type,
	           answers: []
	        };
	    }
    }

    cvaToggleOption(opt) {
      if(this.userAnswers[this.currentQuestionIndex].answers.length > 0){
        for(let opt_d of this.userAnswers[this.currentQuestionIndex].answers) {
          opt_d.selected = false;
        }
        this.userAnswers[this.currentQuestionIndex].answers.length = 0;
      }
      opt.selected = !opt.selected;
      if(opt.selected) {
        this.userAnswers[this.currentQuestionIndex].answers.push(opt);
      }
      else {
        this.userAnswers[this.currentQuestionIndex].answers = this.userAnswers[this.currentQuestionIndex].answers.filter(item => item.value !== opt.value);
      }
    }

    nextQuestion() {  
      if(this.currentQuestion.question_type === 'Open Space'){
        var osansr = {
          id:'1',
          option_value: this.formdata.opnspc_txtbx,
          option_order: 0,
          option_label: this.formdata.opnspc_txtbx,
          statement:''
        }
        this.userAnswers[this.currentQuestionIndex].answers.push(osansr);
        this.formdata.opnspc_txtbx = '';
      }
      this.survey.questions[this.currentQuestionIndex].answered = true;
      this.userAnswers[this.currentQuestionIndex].answered = true;
      if(this.currentQuestionIndex < this.survey.questions.length - 1) {
        this.currentQuestionIndex++;
        this.loadQuestion();
      }else{
        this.lastquecheck = true;
        this.finishSurvey();
      } 
    
    $(".msg_container_base").animate({ scrollTop: $(document).height() }, "slow");
    }
   
   finishSurvey() {
    this.survey.questions[this.currentQuestionIndex].answered = true;
    this.userAnswers[this.currentQuestionIndex].answered = true;
    var surveyObj = {
        userId: this.survey.employee_id,
        projectId: this.survey.project_id,
        userAnswers: this.userAnswers,
        usrveyCode:this.surveyCode
    }
    // console.log(surveyObj);
    console.log('inside finish survey');
    this.dataService.finishBeatsSurvey(surveyObj).subscribe(
        data => {
            if(data.code === 200 && data.survey_status == 'complete') {
                this.finishSurveyView = true;
             }   
             else {
                this.surveyView = 'aerror';
             }

             this.blockUI.stop();
        },
        err => {
            //this.surveyView = 'aerror';
            this.surveyError = 'Survey not submitted';
                this.blockUI.stop();
        });

   }
} 
