import {Component, ViewChild, OnInit, ElementRef,TemplateRef} from '@angular/core';
import { Subject } from 'rxjs';
import { Subscription } from 'rxjs';
import { DataTableDirective } from 'angular-datatables';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Title } from '@angular/platform-browser';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';  
import { Observable } from 'rxjs'; 
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { AuthenticationService } from '../../../services/authentication.service';
import { DataService } from '../../../services/data.service';
import { ExcelServicesService } from '../../../services/excel-services.service';

// import 'rxjs/add/operator/map';
declare var $:any;

//declare var col;
import 'datatables.net';
@Component({
  selector: 'app-tenure',
  templateUrl: './tenure.component.html',
  styleUrls: ['./tenure.component.css']
})
export class TenureComponent implements OnInit {

  
  createTenureForm: FormGroup;
  editTenureForm : FormGroup;
	@BlockUI() blockUI: NgBlockUI;
	submitted = false;

  datatableElement: DataTableDirective;

  @ViewChild('exapmle') table:ElementRef; 
  dtOption:any;
  exapmle:any;
  public tenuredata: any;
  public tenureEditdata:any
  public currentUser: any;
  public showModal:any;
  public tenureData:any;
  public TenureDetails:any;
  public dimensionId:any;
  users: any[] = [];
  dtOptions: DataTables.Settings = {};
  dtTrigger: Subject<any> = new Subject();
  private eventsSub: Subscription;
  dimensionData: any;
  organizationData: any;

  
  constructor(private authService: AuthenticationService,
		private dataService: DataService,
		private title: Title,
    private router: Router,
		private route: ActivatedRoute,
		private excelService:ExcelServicesService,
		private http: HttpClient,
		private formBuilder: FormBuilder) { 
      this.blockUI.start('Loading...'); // Start blocking

      this.tenuredata = {};
    this.tenureEditdata={};
    }

  ngOnInit() {
    this.orgGetAllData();

    this.tenureGetAllData();

    this.dtOption={
      "paging":   true,
      "ordering": true,
      "info": true,
      "autoWidth":false,
      "columnDefs": [
        {
        "targets": [ 0],
        "visible": true,
        "searchable": true
        },
        {
        "targets": [ 1 ],
        "visible": true
        }
    ],
    "pagingType": "full_numbers",
    dom: 'Bfrtip',
        buttons: [
            'copy',
            'csv',
            'excel',
            'pdf',
            'print'
        ]
    };

    

   
    setTimeout(() => {
 
      this.exapmle = $(this.table.nativeElement);
      this.exapmle.DataTable(this.dtOption);
      
    }, 1000);


 this.currentUser = this.authService.currentUserValue;

 
 // console.log(this.currentUser);
 this.createTenureForm = this.formBuilder.group({
  organization: ['', Validators.required],
  tenure:['',Validators.required]
 });

 this.editTenureForm = this.formBuilder.group({
  tenureId:[''],
  organization:[''],
  tenure:['']
 });

  }


  orgGetAllData(){
    this.dataService.getAllOrganization().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.organizationData=data.organization_details;
        //	this.changepanel('queDetails');
         //this.router.navigate(['/Aorganization']);
        
				}
			},
			err => {
				console.log("Orgnization get error", JSON.stringify(err.message));
			}
		);
  }
  tenureFormSubmit() {
		this.submitted = true;
		if (this.createTenureForm.invalid) {
			return;
		}
    //let fileToUpload = this.orglogo.srcElement.files[0];
    
    let tenureFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.tenuredata);

		tenureFormData.append('user_id', this.currentUser.id);
    tenureFormData.append('organization', this.tenuredata.organization);
    tenureFormData.append('tenure', this.tenuredata.tenure);

		

		 //console.log(tenureFormData);
		this.dataService.createTenure(tenureFormData).subscribe(
			data => {
      //  console.log(data);

        this.createTenureForm.reset();
        this.tenureGetAllData();
        
			},
			err => {
				console.log("Tenure Creation error", JSON.stringify(err.message));
			}
		);
  }

  tenureGetAllData(){
    this.dataService.getAllTenure().subscribe(
			data => {
			
				if(data.message == 'Success') {
				//	this.lastProjId = data.project_id;
       // console.log(data);
        this.tenureData=data.tenure_details;
        //	this.changepanel('queDetails');
        this.blockUI.stop();

				}
			},
			err => {
				console.log("Tenure get error", JSON.stringify(err.message));
			}
		);
  }

  tenureEdit(TenureAllData){
  //  console.log(TenureAllData.id);
    this.dataService.getTenureDetails(TenureAllData.id).subscribe(
			data => {
      //console.log(data.Tenure_details);
      this.TenureDetails=data.Tenure_details;

      this.editTenureForm.patchValue({
        tenureId:data.Tenure_details[0]['id'],
        tenure:data.Tenure_details[0]['tenure_cuts'],
        organization:data.Tenure_details[0]['org_id'],

        


      });


				
			}
		);

  }

  tenureFormUpdate() {
		this.submitted = true;
		if (this.editTenureForm.invalid) {
			return;
    }
    
    
    let tenureUpdateFormData: FormData = new FormData();
    
    //console.log(fileToUpload);
    //console.log(this.tenureEditdata);

    tenureUpdateFormData.append('user_id', this.currentUser.id);
    tenureUpdateFormData.append('tenure_id', this.tenureEditdata.tenureId);
    tenureUpdateFormData.append('organization', this.tenureEditdata.organization);
    tenureUpdateFormData.append('tenure', this.tenureEditdata.tenure);

		
		// console.log(tenureUpdateFormData);
		this.dataService.updateTenure(tenureUpdateFormData).subscribe(
			data => {
     //   console.log(data);

          this.tenureGetAllData();

        


			
			},
			err => {
				console.log("Tenure Creation error", JSON.stringify(err.message));
			}
		);
  }

  tenureDelete(TenureAllData){
    Swal.fire({
      title: 'Are you sure?',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes'
      }).then((result) => {
      if (result.value) {
     
        
        this.dataService.deleteTenureDetails(TenureAllData.id).subscribe(
               data => {
             // console.log(data);
              this.tenureGetAllData();
  
            }
           );
  
      }
      });
  
    }
  

}


